.loader {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 1px dashed seagreen;
    animation: rotate 1s infinite linear;
    /*свг и/или анимация*/
}
@keyframes rotate {
    from{
        transform:rotate(0deg) scale(1);
    }
    to{
        transform: rotate(360deg)scale(1.4);
    ;
    }
}